<template>
    <div>
        <TopLink></TopLink>
        <div class="clear"></div>
        <img src="images/timg.gif" class="loding" v-if="guangboData.length==0">
        <div v-else>
            <LoginLink></LoginLink>
            <guangboList :guangboData="guangboData"></guangboList>
        </div>
    </div>
</template>
<script>
import TopLink from "@/components/toplink/toplink.vue"
import LoginLink from "@/components/loginLink/loginLink.vue"
import guangboList from "@/pages/guangbo/guangboList/guangboList.vue"
export default {
    data(){
        return{
            guangboData:[]
        }
    },
    components:{
        TopLink,LoginLink,guangboList
    },
    mounted() {
        this.axios({
            url:"/api/guangbo/gb",
            method:"get"
        }).then(res=>{
            this.guangboData = res.data.data;
        })
    },
}
</script>
